body{
    background: rgb(242,250,255);
    background: linear-gradient(90deg, rgba(242,250,255,1) 0%, rgba(96,186,237,.4) 100%);
}
.color1{
    background-color:#3A3E4A;
    padding:0;
}
.color2{
    background-color:#F2FAFF;
    box-shadow:1px 1px 10px rgba(0, 0, 0, 0.411);
}
h1,h2,h3,h4,h5,p,a{
    font-family: Arial, Helvetica, sans-serif;
    color:#3A3E4A;
}

.navbar-verti{
    background-color:#3A3E4A;
    position:fixed;
    width:inherit;
    height:100%;
}
.navbar-content img{
    margin:50px 0px 0px 50px;
    height:100px;
}

.navbar-nav{
    margin-top:50px;
    padding:0;
}
.navbar-nav .nav-item .nav-link{
    color:white;
    margin-left:20px;
}
.navbar-nav .nav-item{
    transition:all .1s ease-in-out;
    margin:10px 0px;
}
.navbar-nav .nav-item.active{
    background-color:#B832A2;
}
.navbar-nav .nav-item:hover{
    background-color:#B832A2;
    transition:all .1s ease-in-out;
}

.nav-link svg{
   margin:10px;
}


/*navbar hori*/
.navbar-hori{
    padding:10px 0px;
    
}
.title-cms{
    margin-top:10px;
    margin-left:20px;
    font-size:16px;
    color:#3A3E4A;
}
.search-bloc{
    display:flex;
    flex-direction:row;
    max-width:900px;
    background:white;
    border:1px solid rgb(102, 158, 231);
    border-radius:6px;
    margin-top:5px;
}
.search-bloc input{
    width:100%;
    border:none;
    border-top-left-radius: 6px;
    border-bottom-left-radius: 6px;
}
.search-bloc button{
    background:white;
    border:none;
    border-left:1px solid grey;
    border-top-right-radius: 6px;
    border-bottom-right-radius: 6px;
}

.alert-bell{
    background:transparent;
    border:none;
    font-size:20px;
    transition: all .2s ease-in-out;
    border-radius:5px;
}
.alert-bell:hover{
    background:#B832A2;
    transition: all .2s ease-in-out;
    color:white;
}

.right-bloc{
    display:flex;
    flex-direction:row;
    align-items: center;
    justify-content: space-around;
}
.right-bloc img{
    width:10%;
}
.title-cms svg{
    font-size:20px;
    margin:0px 10px;
    margin-top:-5px;
}

/* client page */
.table-content,.form-content{
    margin-top:30px;
}
.table-list{
    border-top-left-radius:10px;
    border-top-right-radius:10px;
    background:#60BAED;
    color:white;
}
.table-item{
    background:#F2FAFF;
    padding:10px 0px;
    border-bottom:.5px solid #3A3E4A;
}
.arrow-col svg{
    color:#60BAED;
    float:right;
}
.action-col svg{
    margin:0px 5px;
}
.email-col p, .phone-col p{
    margin-top:3px;
    font-size:14px;
}
.header-page{
    display:flex;
    flex-direction:row;
    justify-content: space-between;
    margin-top:20px;
}

.header-page button, .btn-form{
    background-color: #B832A2;
    border:none;
    border-radius:5px;
    color:white;
    font-size:14px;
    padding:0px 10px;
}
.btn-form{
    padding:10px 10px;
}
.header-page button:active, .btn-form:active{
    background-color: #7c216d;
}
.message-success.active{
    font-size:20px;
    background-color:rgba(58, 62, 74,.4);
    color:white;
    padding:10px;
    font-weight: 100;
    border-radius:5px;
}

.form-client input, .form-client textarea{
    background:rgba(255,255,255,.7)
}
.form-client input::placeholder, .form-client textarea::placeholder{
    color:#7c216d;
}
.header-title{
    margin-top:10px;
}

/*dashboard*/
.bloc-content{
    background-color:#F2FAFF;
    padding:40px;
    border-radius:10px;
    box-shadow:0px 3px 15px -4px rgba(0,0,0,0.7)
}
.row-content{
    padding:20px 0px;
}
.header-content{
    margin-top:30px;
}
.bloc-content h5{
    text-align:center;
}